/*
//------------------------------------------------------------------------------
// Componente Anuncios
//------------------------------------------------------------------------------
*/

.swipper-anuncios {
	position: relative;
	margin: 0 auto;
	overflow: hidden;
	@include respond-to(m) {
		width: 750px;
	}
	@include respond-to(xl) {
		width: 970px;
	}
	@include respond-to(xxxl) {
		width: 1140px;
	}
	.swiper-wrapper {
		list-style: none;
		padding-left: 0px;
		.field-content{
			figure{
				margin: 0px;
			}
		}
	}
	.pagination-wrap {
		position: relative;
		bottom: 0px;
		width: 100%;
		text-align: center;
	}
	li {
		padding: 0px 15px;
	}
	//imagen
	.views-field-field-image {
		img {
			width: 100%;
		}
		border: 5px solid #fff;
		@include respond-to(xl) {
			float: left;
			width: 50%;
		}
	}
	//contenido
	.anuncios-conten {
		background: #fff;
		border-top: 4px solid color('fucsia');
		padding: 20px;
		overflow: hidden;
		@include respond-to(xl) {
			float: left;
			width: 50%;
			margin: 20px 0 0px;
		}
		//titulo
		.anuncios-titulo {
			h3 {
				font-family: $crimson-textbold;
				@include font-size(ll);
				color: color('grey-dark');
				line-height: 1.1;
				// font-weight: 600;
				text-decoration: none;
			}
		}
		//body
		.anuncios-body {
			p {
				font-family: $crimson-textregular;
				@include font-size(m);
				margin-bottom: 10px;
			}
		}
		//enlaces
		.anuncios-enlace {
			a {
				@include font-size(m);
				color: color('grey-dark');
				text-align: center;
				text-decoration: none;
				padding: 10px 20px;
				border: 1px solid color('grey-dark');
				max-width: 300px;
				display: block;
				float: right;
			}
		}
	}
	.swiper-pagination{
		position: relative;
		display: inline-block;
		text-align: center;
		padding: 15px 0px;
		width: 100px;
		box-sizing: border-box;
		z-index: 1;
		.swiper-pagination-bullet{
			width: 15px;
			height: 15px;
			margin: 0 5px;
			border: 1px solid color(fucsia);
			background-color: white;
			opacity: 1;
			&-active{
				background-color: color(fucsia);
			}
			&:focus{
				outline: none;
			}
			&:not(.swiper-pagination-bullet-active){
				background-color: transparent !important;
			}
		}
	}
	//flechas
	.views-slideshow-controls-bottom {
		text-align: center;
		padding: 0px;
		text-align: center;
		display: block;
		.views_slideshow_pager_field{
			text-align: center;
			.views-slideshow-pager-field-item{
				display: inline-block;
				padding: 2px 5px;
				a {
					background: transparent;
					border: 1px solid color('fucsia');
					border-radius: 10px;
					font-size: 0;
					padding: 0px;
					width: 13px;
					height: 13px;
					display: block;
				}
				&.active{
					a{
						background: color('fucsia');
					}
				}
			}
		}
	}
}